import React, { Component } from 'react'
import {Link} from 'react-router-dom'
import user from '../useraction'
import {withRouter} from 'react-router-dom'
 class SignUnform extends Component {
    submit = () => {
        let first_name = this.refs.first_name.value;
        let last_name = this.refs.last_name.value;
        let user_email = this.refs.email.value;
        let cellno = this.refs.cellno.value;

        let user_password = this.refs.password.value;
        let confirm_pass = this.refs.confirm_password.value;
        alert(first_name)
        if (first_name === "" || last_name === "" || user_email === ""|| cellno==="" || user_password === "" || confirm_pass === "" ) {
           
            alert('all fields are required')
        }
        else if(user_password!==confirm_pass){
    alert('Password donnot xmatch');
        }
        else{
            alert("sucess")
            alert(cellno)
            var url = 'http://localhost:8000/create_user';
            var options = {
                method: 'POST', // or 'PUT'
                body: JSON.stringify({ first_name: first_name, last_name: last_name, email: user_email, cell: cellno, password: user_password }), // data can be `string` or {object}!
                headers: {
                    'Content-Type': 'application/json'
                }
            }

            fetch(url,options)
            .then(res => res.json())
               .then((data)=>{
                   console.log(typeof(data))
                   console.log((data.recieved))
                   console.log(data.user.email)
                   console.log(data.user.password)
                   var useremail = data.user.email
                   var passwor = data.user.password
                   if(data.recieved===true){
                       var op = {
                           method: 'POST',
                           body: JSON.stringify({ username: useremail, password: passwor}),
                           headers: {
                               'Content-Type': 'application/json'
                           }


                    
                      }
                       fetch('http://localhost:8000/login', op)
                      .then((res)=> res.json())
                      .then((data)=> console.log(data))
                      .catch((err)=> console.log(err))
                     
                   }
                //    this.props.history.push('/')
            
               })
                .catch(error => console.error(error));

            }
            
//  var options = {
//                 method:'POST',
//                 body:JSON.stringify({first_name:first_name,last_name:last_name,email:user_email,password:user_password}),
                
//                 headers: {
//                     'Content-Type': 'application/json'
//                 }
//             }
//     fetch('http://localhost:8000/create_user',options)
//             .then((res)=> res.json())
//             .then((data)=> console.log(data))
//             .catch((err) => console.log((err)))
           

    }
          
            
     
    render() {
    
        return (
            <div className="signup-form s1">
                <form>
                    <h2>Register</h2>
                    <p className="hint-text st1">Create your account. It's free and only takes a minute.</p>
                    <div className="form-group s2">
                        <div className="row">
                            <div className="col-xs-6"><input type="text" ref="first_name" className="form-control s3"  placeholder="First Name" /></div>
                                <div className="col-xs-6"><input type="text" ref="last_name" className="form-control s3" placeholder="Last Name" /></div>
                                </div>
                            </div>
                            <div className="form-group s2">
                                <input type="email" ref="email" className="form-control s3"  placeholder="Email" required="required" />
                            </div>
                    <div className="form-group s2">
                        <input type="number" ref="cellno" className="form-control s3" placeholder="Cell No" required="required" />
                    </div>
                            <div className="form-group s2">
                                <input type="password" ref="password" className="form-control s3"  placeholder="Password" required="required" />
                            </div>
                            <div className="form-group s2">
                                <input type="password" ref="confirm_password" className="form-control s3"  placeholder="Confirm Password" required="required" />
                            </div>
                            
                            <div className="form-group s2">
                                <button type="button" onClick={this.submit} className="btn btn-success btn-lg btn-block">Register Now</button>
                            </div>
    </form>
                        <div className="text-center">Already have an account? <Link className="link-singin" to="/singin">Sign in</Link></div>
                    </div>
                    )
                }
}
export default withRouter(SignUnform)