import React, { Component } from 'react'
import Nav from '../home/component/nav'
import Footer from '../home/component/footer'
class Contactus extends Component {
    submit=()=>{
        alert('hu')
var name = this.refs.name.value;
var email = this.refs.email.value;
var message = this.refs.message.value;
if(name===""){
    alert('name required')
    this.refs.name.focus();
    
}
       else if (email === "") {
            alert('email required')
            this.refs.email.focus();

        }
       else if (message === "") {
            alert('message required')
            this.refs.message.focus();

        } else{
            alert('else')
            var options = {
                method:'POST',
                body:JSON.stringify({name:name,email:email,message:message}),
                headers:{
                    'Content-type':'application/json'
                }
            }
    fetch('http://localhost:8000/contact',options)
            .then((res) =>res.json())
            .then((data)=>{
                alert('successfully sent message')
                alert(data)
            })
            .catch((err)=> console.log(err))
        }
    }
    render() {
        return (
            <div>
           <div className="container">
            <Nav/>
                 <div className="row">

            <h2 style={{textAlign:'center',margin:  '100px 0 0 0'}}>Contact us</h2>

            </div>
                 <div className="row">

                 <div className="col-md-3 col-lg-3 col-sm-1"></div>
                 <div className="col-md-6 col-lg-6 col-sm-10">
                        <div class="form-group">
                            <label for="exampleFormControlInput1">Your Name</label>
                            <input type="text" ref="name" class="form-control" placeholder="Please Enter Your Name" />
                        </div>
                        <div class="form-group">
                            <label for="exampleFormControlInput1">Email address</label>
                                <input type="email" ref="email" class="form-control"  placeholder="Please Enter Your Email"/>
  </div>
                        <div class="form-group">
                            <label for="exampleFormControlTextarea1">Your Message</label>
                                <textarea class="form-control" ref="message"  rows="6"></textarea>
                        </div>
                        <button onClick={this.submit} type="button" class="btn btn-primary btn-lg btn-block">Submit</button>

                 </div>
                 <div className="col-md-3 col-lg-3 col-sm-1"></div>
                 </div>
               
             </div>
                <Footer />
            </div>
        )
    }
}
export default Contactus;