import React , {Component} from 'react';
import sale from '../actions/action'
import {Link} from 'react-router-dom'
import { connect } from 'react-redux';

 class Search extends Component{
   
    state = {
        test: 0,
        type: 0
    }
    // findsale function...............................
     findsale=()=>{

         fetch('http://localhost:8000/salefind')
.then((res)=> res.text())
.then((data)=> {
    if(data===""){
        console.log('sale obj')
    }
    else{
    this.props.dispatch(sale(JSON.parse(data)))

        console.log(data)
    }
})
.catch((err)=> console.log(err))
    }
// find rent function..........................
     findrent = ()=>{
         fetch('http://localhost:8000/rentfind')
             .then((res) => res.text())
             .then((data) => {
                 if (data === "") {
                     console.log('rent obj')
                 }
                 else {
                     this.props.dispatch(sale(JSON.parse(data)))

                     console.log(data)
                 }
             })
             .catch((err) => console.log(err))
     }
// find wanted function.................................
     findwanted = ()=>{
         fetch('http://localhost:8000/wantedfind')
             .then((res) => res.text())
             .then((data) => {
                 if (data === "") {
                     console.log('wanted obj')
                 }
                 else {
                     this.props.dispatch(sale(JSON.parse(data)))
                     console.log(data)
                 }
             })
             .catch((err) => console.log(err)) 
     }













    find=()=>{
    let sel1;
    let type1;
    // city
    let city = this.refs.city.value;
    if (this.state.test === 1 && this.state.type === 1) {
        type1 = "home"
        sel1 = this.refs.text.value;
    }
    if (this.state.test === 2 && this.state.type === 2) {
        type1 = "plot"
        sel1 = this.refs.text1.value;
    }
    if (this.state.test === 3 && this.state.type === 3) {
        type1 = "commercial"
        sel1 = this.refs.text2.value;
    }
    
    }
 
    // home style function
    home = () => {
        this.test = 1;
        // Get the checkbox
        var checkBox = this.refs.home;
        // Get the output text
        var text = this.refs.text;
        // commercial1
        var commercial = this.refs.commercial1;
        var plot = this.refs.plot1;
        // If the checkbox is checked, display the output text
        if (checkBox.checked === true) {
            this.setState({ test: 1, type: 1 })
            // this.post()
            text.style.display = "block";
            plot.style.display = "none"
            commercial.style.display = "none"
        } else {
            this.setState({ test: 0, type: 0 })
            text.style.display = "none";
            plot.style.display = "block";
            commercial.style.display = "block";
        }
    }
    // plot style function
    plot = () => {
        // Get the checkbox
        var checkBox = this.refs.plot;
        // Get the output text
        var text = this.refs.text1;
        var home = this.refs.home1;
        var commercial = this.refs.commercial1;

        // If the checkbox is checked, display the output text
        if (checkBox.checked === true) {
            this.setState({ test: 2, type: 2 })
            // this.post()
            text.style.display = "block";
            home.style.display = "none";
            commercial.style.display = "none";
        } else {
            this.setState({ test: 0, type: 0 })
            text.style.display = "none";
            home.style.display = "block";
            commercial.style.display = "block";
        }
    }
    // commercial style function

    commercial = () => {
        // Get the checkbox
        var checkBox = this.refs.commercial;
        // Get the output text
        var text = this.refs.text2;
        var plot = this.refs.plot1
        var home = this.refs.home1;
        // If the checkbox is checked, display the output text
        if (checkBox.checked === true) {
            this.setState({ test: 3, type: 3 })
            // this.post()
            text.style.display = "block";
            plot.style.display = "none";
            home.style.display = "none";
        } else {
            this.setState({ test: 0, type: 0 })
            text.style.display = "none";
            home.style.display = "block";
            plot.style.display = "block";
        }}
    render(){
        return(
            <div className="container">
                <h4 className="search-h1">Search  properties  in Pakistan</h4>
                
                <Link to="/show:salefind"> <button type="button" onClick={this.findsale} className="btn btn-primary btn-lg search-btn1">Buy</button></Link>
                <Link to="/show:rentfind"> <button type="button" onClick={this.findrent} className="btn btn-primary btn-lg search-btn1">Rent</button></Link>
                <Link to="/show:wantedfind">  <button type="button" onClick={this.findwanted} className="btn btn-primary btn-lg search-btn1">Wanted</button></Link>
                <button type="button" className="btn btn-primary btn-lg search-btn1">Project</button>
                {/* <button type="button" className="btn btn-outline-success btn-lg">Success</button> */}

                            

                           
</div>
                
             
            
        )
    }
}
var funn = (store) => {
    return { data: store.landreducer}
}
export default connect(funn)(Search);