import React, {Component} from 'react'
import img1 from '../images/building_construction_facade_117416_1280x720.jpg'
import img2 from '../images/building_construction_facade_117838_1280x720.jpg'
import img3 from '../images/building_facade_architecture_123298_1280x720.jpg'
export default class Minislider extends Component{
    render(){
        return(
            <div className="carousel-wrap">
                <div className="owl-carousel ">
                    <div className="item"><img className="carsoul-mini-detail" src={img1} alt="hi" /></div>
                    <div className="item"><img className="carsoul-mini-detail" src={img2} alt="hi" /></div>
                    <div className="item"><img className="carsoul-mini-detail" src={img3} alt="hi" /></div>
                    <div className="item"><img className="carsoul-mini-detail" src={img3} alt="hi" /></div>
                    <div className="item"><img className="carsoul-mini-detail" src={img3} alt="hi" /></div>
                    <div className="item"><img className="carsoul-mini-detail" src={img3} alt="hi" /></div>


                </div>
            </div>
        )
    }
}